#header-container-sticky {
    position: sticky;
    top:0;
    background-color: rgb(26, 26, 26);
    height: 80px;
    display: flex;
    align-items: center;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.3);
    color: #F9F1E9;
    font-size: 2rem;
    z-index: 1;
}

.name-tag {
    letter-spacing: 1px;
    font-size: 2rem;
}

.link-category {
    font-size: 1.2rem;
}

.link-category {
    margin-left: 2rem;
    cursor: pointer;
    color: #F3613D;
}

#home-wrapper {
    background-color: #111;

    h2 {
        font-size: 3rem;
        margin-top: 100px;
        color: #F9F1E9;
    }

    p {
        color: #F9F1E9;
    }

    canvas {
        position: fixed;
        top: 0;
        height: 100vh;
        pointer-events: none;
    }
}


#footer-closing-tag {
    margin-right: 2rem;
}


footer {
    position: relative;
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(26, 26, 26);
    color: #F9F1E9;
    z-index: 33;

    #tm {
        margin-left: 20px;
    }
}
